<script>
  import Storage from '@/utils/localStorage'
  import * as utils from '@/utils/utils'
  import ChooseGoods from "@/components/Activity/Common/ChooseGoods"
  import ChooseStore from "@/components/Activity/Common/ChooseStore"
  import AddByExcel from "@/components/Common/Excel/AddByExcel"
  import * as qiniu from 'qiniu-js'
  import exportSheet from '@/utils/exportExcel'
  const numReg = /^[1-9]*[1-9][0-9]*$/
  const salePriceReg = /((^[1-9]\d*)|^0)(\.\d{0,2}){0,1}$/
  const fileUrlList = {
    getQiNiuToken: '/foundation/qiniu/auth/file'
  }
  const requestUrl = {
    // 保存活动
    saveActivity: '/plateform/a/preSale/activity/saveActivity',
    // 更新活动
    updateActivity: '/plateform/a/preSale/activity/updateActivity',
    // 详情
    detail: '/plateform/a/preSale/activity/detail',
    // 校验新增预售商品
    checkPreGoodsInsert: '/newretail-shop/a/preSale/goods/checkPreGoodsInsert'
  }
  export default {
    data() {
      return {
        editType: '', // 编辑类型
        activityId: '', // 活动id
        isDisabled: false,
        addParamMsg: {
          platformId: 5,
          DIFFCITY: true,
          regionCode: '',
          regionName: '',
          activityName: '', // 活动名称
          startDatetime: '', // 开始时间cycleType
          endDatetime: '', // 结束时间
          activityType: 51, // 预售类型，固定值-51
          presaleType: 2, // 预售类型，1-单期，2-周期
          cycleType: 1, // 周期类型，1-短周期，2-长周期
          shortCycleType: 1, // 周期类型  1 每天 2 每两天
          longCycleType: 3, // 周期类型  周三
          cutoffTime: '',
          shortPickUpType: 0, // 提货类型 截单后第2天提
          longPickUpType: 5, // 提货类型 周五

          pickUpStartDate: '', // 提货开始时间
          pickUpEndDate: '', // 提货结束时间
          goodsReqList: [],
          storeNoList: []
        },
        shortCircleOptions: [
          {
            label: '每天',
            value: 1
          },
          {
            label: '每2天',
            value: 2
          }
        ],
        shortPickUpTypeOptions: [
          {
            label: '截单后第2天提',
            value: 0
          }
        ],
        longCycleOptions: [
          {
            label: '周一',
            value: 1
          },
          {
            label: '周二',
            value: 2
          },
          {
            label: '周三',
            value: 3
          },
          {
            label: '周四',
            value: 4
          },
          {
            label: '周五',
            value: 5
          },
          {
            label: '周六',
            value: 6
          },
          {
            label: '周日',
            value: 7
          }
        ],
        pickerOptions: {
          disabledDate(time) {
            return time.getTime() < Date.now() - 8.64e7
          }
        },
        // 选中的商品列表
        checkedList: [],
        // 商品详情图加载
        imageUploadLoading: false,
        currentUpFileIdx: -1, // 默认选中的文件上传框
        // 参与门店选中的
        activityStoresCheckedList: [],
        // 活动状态 0：未发布，1 ：已发布 ，2：未开始，3：进行中，4：已结束，5：已终止，6：已失效"
        status: 0,
        tempConfig: {
          platformCityGoodsId: '区域商品id(必填)',
          goodsNo: 'SKU编码(选填)',
          skuNo: 'ERP商品编码(选填)',
          goodsName: '活动商品名称(可修改，必填)',
          price: '原价(必填）',
          preSalePrice: '预售价(必填)',
          platformCurStock: '区域限售库存(选填，为空时不限)',
          stock: '门店限售库存(选填，为空时不限)',
          limitCount: '每人每期限购(选填，为空时不限)',
          sortNum: '排序(必填)',
          sfInStockStr: '顺丰仓现货（选填，填是/否，空时为是）'
        },
        // 显示下载含有失败原因的excel
        showTipErrorMsg: false, 
        // 后台返回的又失败原因的内容
        hasErrorMsgData: []
      }
    },
    components: {
      "excel-add": AddByExcel,
      "choose-goods": ChooseGoods,
      "choose-store": ChooseStore
    },
    beforeRouteEnter (to, from, next) {
      if(to.params.type === 'copy') {
        document.title = "复制预售活动"
        to.meta.breadcrumbList =[
          {name: '预售管理', path: ''},
          {name: '预售活动管理', path: '/pre_act'},
          {name: '复制预售活动', path: ''}
        ]
      }else {
        document.title = "编辑预售活动"
        to.meta.breadcrumbList =[
          {name: '预售管理', path: ''},
          {name: '预售活动管理', path: '/pre_act'},
          {name: '编辑预售活动', path: ''}
        ]
      }
      next()
    },
    created() {
      this.activityId = this.$route.params.id
      this.editType = this.$route.params.type
      this.status = this.$route.query.status ? parseInt(this.$route.query.status) : 0
    },
    mounted() {
      // 获取详情
      this.getDetailMsg()
    },
    methods: {
      // 获取详情
      getDetailMsg() {
        utils.showLoading()
        this.$http.post(requestUrl.detail, {activityId: this.activityId, DIFFCITY: true}).then(res => {
          utils.clearLoading()
          if(res.code === 0) {
            if(res.data) {
              this.addParamMsg.regionCode = res.data.regionCode
              this.addParamMsg.regionName = res.data.regionName
              this.addParamMsg.activityName = res.data.activityName
              if(res.data.activityType) this.addParamMsg.activityType = res.data.activityType || 51
              if(res.data.presaleType) this.addParamMsg.presaleType = res.data.presaleType
              if(res.data.cycleType) this.addParamMsg.cycleType = res.data.cycleType

              if(res.data.activityStartDate) {
                this.addParamMsg.startDatetime = res.data.activityStartDate
              }else {
                this.addParamMsg.startDatetime = ""
              }
              if(res.data.activityEndDate) {
                this.addParamMsg.endDatetime = res.data.activityEndDate
              }else {
                this.addParamMsg.endDatetime = ""
              }

              if(parseInt(res.data.presaleType) === 1) {
                this.addParamMsg.pickUpStartDate = res.data.pickUpDate
              }else if(parseInt(res.data.presaleType) === 2) {
                if(parseInt(res.data.cycleType) === 1) {
                  // 短周期
                  this.addParamMsg.shortCycleType = res.data.cutoffDays ? parseInt(res.data.cutoffDays) : 1
                  this.addParamMsg.shortPickUpType = 0
                }else if(parseInt(res.data.cycleType) === 2){
                  // 长周期
                  this.addParamMsg.longCycleType = res.data.cutoffType
                  this.addParamMsg.longPickUpType = res.data.pickUpType
                }
              }
              this.addParamMsg.cutoffTime = res.data.cutoffTime

              if(res.data.storeNoList && res.data.storeNoList.length) {
                this.activityStoresCheckedList = res.data.storeNoList
              }
              this.checkedList = res.data.goodsVoList && res.data.goodsVoList.length > 0 ? res.data.goodsVoList : []
              this.checkedList.forEach(item => {
                item.disabled = this.status === 2 || this.status === 3
                item.sfInStock = item.sfInStock
              })
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          utils.clearLoading()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doSaveHandel 保存]
       * @return {[type]} [description]
       */
      doSaveHandel() {
        if(!this.addParamMsg.activityName) {
          this.$message("请输入预售周期名称")
          return
        }else {
          if(this.addParamMsg.activityName.length > 20) {
            this.$message("预售周期名称,限制20个字")
            return
          }
        }
        // 起止日期
        if(!this.addParamMsg.startDatetime) {
          this.$message("请选择开始时间")
          return
        }
        if(!this.addParamMsg.endDatetime) {
          this.$message("请选择结束时间")
          return
        }
        let startDatetimeNum = utils.changeDateToTimestamp(this.addParamMsg.startDatetime)
        let endDatetimeNum = utils.changeDateToTimestamp(this.addParamMsg.endDatetime)
        if(endDatetimeNum <= startDatetimeNum) {
          this.$message("结束时间不能早于或等于开始时间")
          return
        }
        if(parseInt(this.addParamMsg.presaleType) === 1) {
          // 单期
          // 提货起止时间
          if(!this.addParamMsg.pickUpStartDate) {
            this.$message("请选择提货日期")
            return
          }
          let pickUpStartDateNum = utils.changeDateToTimestamp(this.addParamMsg.pickUpStartDate)
          if(pickUpStartDateNum <= endDatetimeNum) {
            this.$message("提货日期不能早于结束时间")
            return
          }
        }else if(parseInt(this.addParamMsg.presaleType) === 2) {
          // 周期
          if(!this.addParamMsg.cutoffTime) {
            this.$message("请选择截单时间")
            return
          }
        }

        let storeNoList = this.activityStoresCheckedList.map(item => {
          return item.erpStoreNo
        })
        this.addParamMsg.storeNoList = storeNoList
        if(this.addParamMsg.storeNoList.length === 0) {
          this.$message("请选择参与门店")
          return
        }
        let goodsReqListArr = []
        for(let item of this.checkedList) {
          // if(!item.preSaleImageUrl) {
          //   this.$message(`请上传(${item.goodsName})的商品图片`)
          //   return 
          // }
          if(item.goodsName === '') {
            this.$message(`请输入(${item.goodsName})的商品名称`)
            return
          }
          if(!item.price) {
            this.$message(`请输入(${item.goodsName})的原价`)
            return
          }else {
            if(!salePriceReg.test(item.price) || item.price == 0) {
              this.$message('原价是大于0，小数保留小数点后2位') 
              return
            }
          }
          if(!item.preSalePrice) {
            this.$message(`请输入(${item.goodsName})的预售价`)
            return
          }else {
            if(!salePriceReg.test(item.preSalePrice) || item.preSalePrice == 0) {
              this.$message('预售价是大于0，小数保留小数点后2位') 
              return
            }
          }

          if(parseFloat(item.preSalePrice) > parseFloat(item.price)) { 
            this.$message(`(${item.goodsName})的原价必须大于等于预售价`)
            return
          }

          if(item.platformCurStock != 0 && item.platformCurStock) {
            if(numReg.test(item.platformCurStock)) {
              if(item.platformCurStock < 0) {
                this.$message("区域限售库存不能小于0")
                return
              }else if(item.platformCurStock > 9999) {
                this.$message("区域限售库存不能大于9999")
                return
              }
            }else {
              this.$message("门店限售库存格式不正确")
              return
            }
          }

          if(item.stock != 0 && item.stock) {
            if(numReg.test(item.stock)) {
              if(item.stock < 0) {
                this.$message("门店限售库存不能小于0")
                return
              }
            }else {
              this.$message("门店限售库存格式不正确")
              return
            }
          }

          if(item.limitCount != 0 && item.limitCount) {
            if(numReg.test(item.limitCount)) {
              if(item.limitCount < 0) {
                this.$message("限购数量不能小于0")
                return
              }
            }else {
              this.$message("输入限购数量格式不正确")
              return
            }
          }

          if(item.sortNum != 0 && item.sortNum) {
            if(numReg.test(item.sortNum)) {
              if(item.sortNum < 0 || item.sortNum > 299) {
                this.$message("排序权重为0 — 299的整数，越大越靠前")
                return
              }
            }else {
              this.$message("排序权重为0 — 299的整数，越大越靠前")
              return
            }
          }

          let goodsReqListItem = {
            preSaleImageUrl: item.preSaleImageUrl,
            goodsId: item.goodsId,
            goodsNo: item.goodsNo,
            skuNo: item.skuNo,
            goodsName: item.goodsName,
            preSalePrice: item.preSalePrice,
            price: item.price,
            stock: item.stock,
            platformCurStock: item.platformCurStock,
            limitCount: item.limitCount,
            sortNum: item.sortNum,
            spec: item.spec,
            platformCityGoodsId: item.platformCityGoodsId,
            sfInStock: item.sfInStock
          }
          goodsReqListArr.push(goodsReqListItem)
        }
        if(goodsReqListArr.length === 0) {
          this.$message("请先设置预售商品")
          return
        }
        let params = {
          DIFFCITY: this.addParamMsg.DIFFCITY,
          regionCode: this.addParamMsg.regionCode,
          activityName: this.addParamMsg.activityName,
          presaleType: this.addParamMsg.presaleType,
          activityType: this.addParamMsg.activityType,
          startDatetime: this.addParamMsg.startDatetime,
          endDatetime: this.addParamMsg.endDatetime,
          storeNoList: this.addParamMsg.storeNoList,
          goodsReqList: goodsReqListArr
        }
        if(parseInt(this.addParamMsg.presaleType) === 2) {
          // 周期
          if(parseInt(this.addParamMsg.cycleType) === 1) {
            // 短周期
            params.cycleType = this.addParamMsg.cycleType
            params.cutoffType = 0
            params.cutoffDays = this.addParamMsg.shortCycleType
            params.cutoffTime = this.addParamMsg.cutoffTime
            params.pickUpType = this.addParamMsg.shortPickUpType
          }else {
            // 长周期
            params.cycleType = this.addParamMsg.cycleType
            params.cutoffType = this.addParamMsg.longCycleType
            params.cutoffTime = this.addParamMsg.cutoffTime
            params.pickUpType = this.addParamMsg.longPickUpType
          }
        }else {
          // 单期
          params.pickUpStartDatetime = this.addParamMsg.pickUpStartDate + ' 00:00:00'
        }
        // 请求接口  区分更新还是复制
        let saveOrUpdateUrl = requestUrl.updateActivity 
        let successTip = ''
        if(this.editType === 'edit') {
          // 编辑
          params.activityId = this.activityId
          saveOrUpdateUrl = requestUrl.updateActivity 
          successTip = '编辑成功'
        }else if(this.editType === 'copy') {
          // 复制
          saveOrUpdateUrl = requestUrl.saveActivity 
          successTip = '复制成功'
        }
        this.isDisabled = true
        this.$http.post(saveOrUpdateUrl, {...params}).then(res => {
          // 保存按钮可再次点击
          setTimeout(() => {
            this.isDisabled = false
          }, 800)
          if(res.code === 0) {
            this.$message.success(successTip)
            this.$router.push('/pre_act')
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.isDisabled = false
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })
      },
      /**
       * [doCancelHandel 取消]
       * @return {[type]} [description]
       */
      doCancelHandel() {
        this.$confirm('确定取消吗?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.$router.push('/pre_act')
        }).catch(() => {})
      },
      /**
       * [goAddGoodsDialog 添加商品]
       * @return {[type]} [description]
       */
      goAddGoodsDialog() {
        if(this.$refs.chooseGoods) {
          let _params = {
            title: '选择商品',
            regionCode: this.addParamMsg.regionCode,
            checkedList: this.checkedList
          }
          this.$refs.chooseGoods.setDefault(_params)
        }
      },
      /**
       * [confirmChooseGoods 选中商品并关闭]
       * @param  {[type]} val [description]
       * @return {[type]}      [description]
       */
      confirmChooseGoods(val) {
        this.checkedList = val.checkedList || []
      },
      /**
       * [deleteItem 删除]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteItem(row) {
        let platformCityGoodsId = row.platformCityGoodsId
        let resCheckedListArr = this.checkedList.filter(item => {
          return item.platformCityGoodsId != platformCityGoodsId
        })

        this.checkedList = JSON.parse(JSON.stringify(resCheckedListArr))
      },

      /**
       * @description: 设置当前上传图片的索引
       * @param {type} 
       * @return: 
       */
      setItemIdx(idx) {
        if(this.imageUploadLoading) return
        this.currentUpFileIdx = idx
      },
      /**
       * [emitUpload 上传图片]
       * @param  {[type]} e [description]
       * @return {[type]}   [description]
       */
       emitUpload(e) {
        this.imageUploadLoading = true
        this.getToken().then((res) => {
          // 开始上传图片
          let uploader = qiniu.upload(e.file, res.fileName, res.token)
          uploader.subscribe({
            error: () => {
              this.imageUploadLoading = false
            },
            complete: (res) => {
              // 添加内容设置图片地址
              let urlImage = 'http://retailimg01.benlailife.com/' + res.key
              this.imageUploadLoading = false
              // 设置对应的图片地址
              let checkedListArr = [...this.checkedList]
              checkedListArr.forEach(item => {
                if(item.goodsId === this.currentUpFileIdx) {
                  item.preSaleImageUrl = urlImage
                }
              })
              this.checkedList = checkedListArr
            },
            next: res => {
            }
          })
        })
      },

      /**
       * [imageUploadReg 上传图片检测]
       * @param  {[type]} file [description]
       * @return {[type]}      [description]
       */
      imageUploadReg (file) {
        const isPic = file.type.indexOf('image/') !== -1
        const isJPG = file.type === 'image/jpeg' || 'image/png'
        const isSizeNum = file.size <= 1 * 300 * 1024
        if (!isPic || !isJPG || !isSizeNum) {
          this.$message.error('商品图片建议上传大小不超过300K的710*420像素的jpg或png图片')
        }
        return isPic && isJPG && isSizeNum
      },
      /**
       * [getToken 通过后台封装的接口获取七牛的token]
       * @return {[type]}            [description]
       */
      getToken () {
        return this.$http.post(fileUrlList.getQiNiuToken, {}).then(res => {
          if (res.code == 0) {
            let token = res.data.token
            let fileName = res.data.key
            return {fileName, token}
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.imageUploadLoading = false
          this.$notify.error({
            title: '获取七牛token提示',
            message: e.message
          })
        })
      },
      platformCurStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>区域限售库存，即所有门店共享的总预售库存；</p>
              <p>门店限售即每个门店的预售库存，可在门店预售商品中调整门店库存；</p>
              <p>每个商品至少要设置一种限售库存方式，最大可设置9999；</p>
              <p>如果既设了区域限售，又设了门店限售，则先校验区域库存，再校验门店库存；</p>
            </div>
            <span slot="reference">区域限售库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      limitCountHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover"
            content="短周期为每人每天限购数量；长周期为每人每周限购数量；单期预售为每人活动期间限购数量；为空时不限；">
            <span slot="reference">每人每期限购<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      sortNumHeader() {
        return (
          <el-popover
            placement="top-start"
            trigger="hover"
            content="0-299 越大越靠前">
            <span slot="reference">排序权重<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      sfInStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>1、顺丰标品设为顺丰仓现货时，下单会校验仓库库存；</p>
              <p>2、本来鲜生鲜品无需设置；</p>
            </div>
            <span slot="reference">是否为顺丰仓现货<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [copyUrl 复制链接成功]
       * @return {[type]}      [description]
       */
       onCopySuc() {
        this.$message.success("链接已复制至剪切板，仅限微信公众号中使用")
      },
      /**
       * [onCopyError 复制链接失败]
       * @return {[type]} [description]
       */
      onCopyError() {
        this.$message.error("复制链接失败,请手动复制")
      },
      platformCurStockHeader() {
        return (
          <el-popover
            placement="top-start"
            width="260"
            trigger="hover">
            <div>
              <p>区域限售库存，即所有门店共享的总预售库存；</p>
              <p>门店限售即每个门店的预售库存，可在门店预售商品中调整门店库存；</p>
              <p>每个商品至少要设置一种限售库存方式，最大可设置9999；</p>
              <p>如果既设了区域限售，又设了门店限售，则先校验区域库存，再校验门店库存；</p>
            </div>
            <span slot="reference">区域限售库存<i class="el-icon-question"></i></span>
          </el-popover>
        )
      },
      /**
       * [chooseStoreOpen 选择门店]
       * @return {[type]} [description]
       */
       chooseStoreOpen() {
        let params = {
          platformId: this.addParamMsg.platformId,
          regionCode: this.addParamMsg.regionCode,
          title: '选择门店',
          checkedList: this.activityStoresCheckedList
        }
        this.$refs.chooseStore.setDefault(params)
      },
      /**
       * [deleteItem 删除选中的门店]
       * @param  {[type]} _row [description]
       * @return {[type]}      [description]
       */
      deleteStoreItem(_row) {
        let erpStoreNo = _row.erpStoreNo
        this.activityStoresCheckedList = this.activityStoresCheckedList.filter(item => {
          return item.erpStoreNo !== erpStoreNo
        })
      },
      /**
       * [confirmChooseStore 选中的门店]
       * @param  {[type]} value [description]
       * @return {[type]}        [description]
       */
      confirmChooseStore(value) {
        let activityStoresCheckedList = []
        if(value.checkedList && value.checkedList.length) {
          activityStoresCheckedList = value.checkedList
        }else {
          activityStoresCheckedList = []
        }
        this.activityStoresCheckedList = activityStoresCheckedList
      },
      /**
       * @description: 导入excel后确认框
       * @param {type} 
       * @return: 
       */
      doConfirmHandel(value) {
        let excelDataList = value.dataList || []
        excelDataList.forEach(item => {
          item.activityType = 51
        })
        this.$http.post(requestUrl.checkPreGoodsInsert, [...excelDataList]).then(res => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          if(res.code === 0) {
            this.hasErrorMsgData = res.data && res.data.length ? res.data : []
            let isOk = this.hasErrorMsgData.some(item => {
              return item.msgType === 1
            })
            if(isOk) {
              this.showTipErrorMsg = true
            }else {
              let checkedListTemp = [...this.checkedList]
              checkedListTemp.forEach(i => {
                this.hasErrorMsgData.forEach((j,idx) => {
                  if(i.goodsId === j.goodsId && i.goodsNo === j.goodsNo && i.platformCityGoodsId === j.platformCityGoodsId && i.skuNo === j.skuNo) {
                    i.goodsName = j.goodsName
                    i.spec = j.spec
                    i.preSaleImageUrl = j.preSaleImageUrl
                    i.price = j.price
                    i.preSalePrice = j.preSalePrice
                    i.platformCurStock = j.platformCurStock
                    i.stock = j.stock
                    i.limitCount = j.limitCount
                    i.sortNum = j.sortNum
                    i.sfInStock = j.sfInStock
                  }
                })
              })
              let hash = {}
              this.checkedList = [...checkedListTemp, ...this.hasErrorMsgData].reduceRight((item, next) => {
                hash[next.platformCityGoodsId] ? '' : hash[next.platformCityGoodsId] = true && item.push(next)
                return item
              }, [])
            }
            return
          }
          throw new Error(res.msg)
        }).catch(e => {
          this.$refs.excelAdd.closeDisabled()
          this.$refs.excelAdd.closeDialog()
          this.$notify.error({
            title: '提示',
            message: e.message
          })
        })  
      },
      showExcelBatchInsertDialog() {
        if(this.$refs.excelAdd) {
          let val = {
            title: 'Excel批量导入',
            tempConfig: this.tempConfig
          }
          this.$refs.excelAdd.setDefaultMsg(val)
        }
      },
      downLoadErrorMsg() {
        let downloadExcelConfig = {...this.tempConfig}
        downloadExcelConfig.msgTypeStr = '验证结果'
        downloadExcelConfig.msg = '失败原因'
        this.hasErrorMsgData.forEach(item => {
          item.msgTypeStr = item.msgType === 1 ? '验证失败' : '验证成功'
          item.sfInStockStr = item.sfInStock ? '是' : '否'
        })
        const tHeader = utils.objectToArray(downloadExcelConfig, false)
        const filterVal = utils.objectToArray(downloadExcelConfig, true)
        exportSheet.ExportJsonToExcel({
          header: tHeader,
          filterVal,
          data: this.hasErrorMsgData,
          filename: `Excel批量导入模板`
        })
        setTimeout(() => {
          this.$notify.success({
            title: '温馨提示',
            message: `Excel批量导入模板下载成功`
          })
          this.isDisabled = false
        }, 500)
      }
    },
    /**
     * [beforeRouteLeave 页面离开]
     * @param  {[type]}   to   [description]
     * @param  {[type]}   from [description]
     * @param  {Function} next [description]
     * @return {[type]}        [description]
     */
    beforeRouteLeave (to, from, next){
      to.meta.keepAlive = true;
      next();
    }
  }
</script>


